<!-- 精彩视频 -->
<template>
  <div class="wonderfulVideo">
      <!-- <div class="wonderfulVideo_title" v-if="videoImg.length!==0"><span>推广视频</span> </div>
      <ul class="voideo_list">
        <li v-for="(item,index) in videoImg" :key="index" @click="tovideoDetail(item.value)">
          <img class="bofangBtn" :src="staticImgH+'bofangBtn.png'" alt="">
          <img class="videoImg"  alt="" :src="item.img">
        </li>
      </ul> -->

      <div class="player_style_tuijian" v-if="PlayerStyleData.length!==0"><span>精彩节目推送</span><span class="more" @click="lookMore">查看更多</span></div>
     <ul class="player_list">
        <li @click="toShowDetails(item.id)" v-for="(item,index) in PlayerStyleData" :key="index">
          <img :src="item.image" alt="">
          <div class="player_top">
              <span class="player_top_userName">节目：{{item.username}}</span>
              <span>票数：{{item.votes}}</span>
          </div>
        </li>
      </ul>
  </div>
</template>

<script>
import qs from 'qs'
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
export default {
  data () {
    return {
      videoImg:'',
      PlayerStyleData:'',
    };
  },

  components: {},

  computed:{
        ...mapState(['staticImgH'])
    },

  mounted(){
    // this.$http.post('/chunwan/first/first_index').then((res)=>{
    //       this.videoImg=res.data.data.data
    // })
    this.getPageVideo()
  },

  methods: {
    // 查看更多
    lookMore(){
        this.SearchConditions('')
        this.barIdINdexs('')
        this.$router.push('/ProgramListRanking')
    },
    goGoodsPage(player) {
          this.playerVideoPages('/')   //设置选手视频返回页面
          this.PlayerStyleDetailedPlayer(player);  //给选手视频页面传player
          this.$router.push('/PlayerStyleDetailed') 
      },
      //   跳节目详情
      toShowDetails(id) {
          this.playerIds(id)//保存选手id
          this.addressIdIsSels('false') //投票盒子不显示 
          this.ShowDetailsPages('/')  //选手详情返回页面
          this.playDetailVoteDivs('false') //选手详情的投票盒子的消失
          this.$router.push('/ShowDetails')
      },
    tovideoDetail(itemValue){
      this.$router.push({path:'/videoDetail',query:{videoSrc:itemValue}})
    },
    getPageVideo() {
        let obj = qs.stringify({
          page: 1,
          limit: 6
        })
        this.$http.post('/chunwan/entering/index/0', obj).then((res) => {
          if (res.data.code == 200) {
            this.PlayerStyleData = res.data.data.data
          } else {
            this.toastMsg(res.data.msg)
            return false;
          }
        })
      },
    // 弹框提示
      toastMsg(msg) {
        let self = this
        clearInterval(self.timer2);
        this.promptContent = msg
        this.showPrompt = true
        self.timer2 = setTimeout(function () {
          self.showPrompt = false
          clearInterval(self.timer2);
        }, 2000)
      },
       ...mapMutations(['playerIds', 'addressIdIsSels','ShowDetailsPages', 'playDetailVoteDivs', 'PlayerStyleDetailedPlayer','playerVideoPages','SearchConditions','barIdINdexs']),
  }
}

</script>
<style scoped lang="stylus">
.wonderfulVideo{
    font-size:0.427rem;
}
.wonderfulVideo_title{
  font-size:0.427rem;
  color:rgba(0, 0, 0, 1);
  display :flex;
  align-items :center;
  margin-bottom:0.32rem;
  margin-top:0.4rem;
  >span{
    margin-left:0.4rem;
    padding-bottom:0.2rem;
    border-bottom:0.053rem solid  rgba(255, 204, 212, 1);
    color:rgba(0, 0, 0, 1);
    font-weight:600;
  }
}
.voideo_list{
  >li{
    width:100%;
    height:5.33rem;
    position :relative;
    margin-bottom:0.32rem;
    >.videoImg{
       width:100%;
       height:100%;
    }
    >.bofangBtn{
      width:0.97rem;
      height:0.97rem;
      position :absolute;
      left:50%;
      top:50%;
      margin-left:-0.453rem;
      margin-top:-0.453rem;
    }
  }
}



.player_style_tuijian{
    display:flex;
    align-items:center;
    justify-content: space-between;
    padding:0 0.36rem;
    font-size:0.427rem; 
    font-weight:550; 
    margin-top:0.5rem; 
    >span{
        padding-bottom: 0.2rem;
        border-bottom: 0.053rem solid #ffccd4;
        color: #000;
        font-weight: 600;
    }
    >.more{
      font-size:0.37rem;
      color: #000;
      padding-bottom: 0.2rem;
      border-bottom: 0;
    }
}
.gengduo{ width:100%; height:2rem; margin-top:0.4rem; font-size:0.4rem;
>.gengduo_left{ margin-left:4.28rem; text-align:center; margin-top:1rem;}
>.gengduo_right{ width:100%; text-align:center;}
}


.player_list{
    padding:0 0.4rem;
    padding-top:0.32rem;
	  display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    >li{
        width:4.44rem;
        margin-bottom:0.4rem;
        border-radius:0.16rem;
        background :rgba(255, 252, 237, 1);
        >img{
          width:4.44rem;
          height:4.44rem;
          border-radius:0.16rem 0.16rem 0 0;
        }
        >.player_top{
            display :flex;
            flex-direction :column;
            margin-bottom:0.313rem;
			      margin-left:0.14rem;
            >.player_top_userName{
                margin: 0.2rem 0;
            }
            >span{
                font-size:0.3rem;
                color:rgba(0, 0, 0, 0.8);
                font-weight:550;
            }
        }
    }
}
</style>


